import React from 'react';
// import bannerLogin from '@/assets/singin-banner.png';
import { ReactComponent as AppleSocial } from '@/assets/apple-social.svg';
import { ReactComponent as FacebookSocial } from '@/assets/facebook-social.svg';
import { ReactComponent as GoogleSocial } from '@/assets/google-social.svg';
import { Box, FormHelperText, Grid, Paper, Typography } from '@mui/material';
import useMediaQuery from '@/services/useMediaQuery';
import { LoginForm } from './components/LoginForm';
import { LoginRequest } from '@/@types';

interface LoginPageProps {
  errorMessage?: string;
  onLogin: (data: LoginRequest) => void;
  socialLogin: (social: 'facebook' | 'google' | 'apple') => void;
}

function LoginPage({ errorMessage, onLogin, socialLogin }: LoginPageProps) {
  const { isMobile } = useMediaQuery();
  return (
    <Box display="flex" justifyContent="center" alignItems="center" height="100vh">
      <Paper
        elevation={3}
        sx={{
          borderRadius: isMobile ? 0 : 4,
          width: '100%',
          maxWidth: 496,
          height: isMobile ? '100vh' : 'unset',
          display: isMobile ? 'flex' : 'block',
          flexDirection: 'column',
          justifyContent: 'space-between',
        }}
      >
        <Box>
          <Box
            sx={{
              maxWidth: '100%',
              height: 'auto',
              backgroundColor: '#DFB424',
              borderRadius: 4,
              borderTopLeftRadius: isMobile ? 0 : 16,
              borderTopRightRadius: isMobile ? 0 : 16,
            }}
          >
            <img
              src="https://raw.githubusercontent.com/viktor1910/login-form/main/public/singin-banner.png"
              alt="login-banner"
              style={{ width: '100%', height: 'auto', display: 'block' }}
            />
          </Box>
          <Box
            sx={{
              px: isMobile ? 4 : 10,
              pt: isMobile ? 6 : 10,
              pb: isMobile ? 20 : 10,
            }}
          >
            {errorMessage && (
              <Box pb={10}>
                <FormHelperText error>{errorMessage}</FormHelperText>
              </Box>
            )}
            <LoginForm onLogin={onLogin} />
          </Box>
        </Box>

        <Box marginBottom={4}>
          <Typography variant="body2" color="#7D848D" textAlign="center">
            Or sign in with
          </Typography>
          <Grid container spacing={6} justifyContent="center" mt={0}>
            {isMobile && (
              <Grid item>
                <FacebookSocial onClick={() => socialLogin('facebook')} />
              </Grid>
            )}

            <Grid item>
              <GoogleSocial onClick={() => socialLogin('google')} />
            </Grid>
            {isMobile && (
              <Grid item>
                <AppleSocial onClick={() => socialLogin('apple')} />
              </Grid>
            )}
          </Grid>
        </Box>
      </Paper>
    </Box>
  );
}

export default LoginPage;
